import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import Logo from '../../img/logo.jpg'
import './Header.scss'

export default function Header() {
  const [mobileMenu, setMobileMenu] = useState(false)

  const menuHandler = () => setMobileMenu(!mobileMenu)

  return (
    <header className="header">
      <img className="logo" src={Logo} alt='sorry bro :(' />
      <nav className="header__nav-desc" >
        <NavLink className="header__nav" to="/">Главная</NavLink>
        <NavLink className="header__nav" to="contacts">Контакты</NavLink>
        <NavLink className="header__nav" to="prices">Цены</NavLink>
        <NavLink className="header__nav" to="gallery">Галерея</NavLink>
      </nav>
      <nav style={mobileMenu ? {display: "flex"} : {display: "none"}} className="header__nav-desc-mobile" >
        <NavLink className="header__nav-mobile" to="/">Главная</NavLink>
        <NavLink className="header__nav-mobile" to="contacts">Контакты</NavLink>
        <NavLink className="header__nav-mobile" to="prices">Цены</NavLink>
        <NavLink className="header__nav-mobile" to="gallery">Галерея</NavLink>
      </nav>
      <div onClick={menuHandler} className="header__mobile-menu"></div>
    </header>
  )
}

